const convertDropdownValue = (dropdownvalue: string): string => {
  const now = 'NOW - ';
  switch (dropdownvalue) {
    case '1 da':
      return `${now}1day`;
    case '3 das':
      return `${now}3days`;
    case '10 das':
      return `${now}10days`;
    case '20 das':
      return `${now}20days`;
    case '30 das':
      return `${now}30days`;
    default:
      return `${now}1day`;
  }
};

export default convertDropdownValue;
